// src/app/categories/page.tsx
'use client'

import { useState } from 'react'
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query'
import { Button } from '@/components/ui/Button'
import { fetchCategories, deleteCategory } from '@/lib/api'
import {
    AlertDialog,
    AlertDialogAction,
    AlertDialogCancel,
    AlertDialogContent,
    AlertDialogDescription,
    AlertDialogFooter,
    AlertDialogHeader,
    AlertDialogTitle,
} from "@/components/ui/alert-dialog"

interface Category {
    id: number
    name: string
    description: string | null
    materials: any[]
}

export default function CategoriesPage() {
    const [categoryToDelete, setCategoryToDelete] = useState<Category | null>(null)
    const queryClient = useQueryClient()

    const {
        data: categories,
        isLoading,
        isError,
        error
    } = useQuery<Category[]>({
        queryKey: ['categories'],
        queryFn: fetchCategories
    })

    const deleteMutation = useMutation({
        mutationFn: (id: number) => deleteCategory(id),
        onSuccess: () => {
            // Invalidate and refetch categories after deletion
            queryClient.invalidateQueries({ queryKey: ['categories'] })
        }
    })

    const handleAddCategory = () => {
        alert('Add new category clicked')
    }

    const handleDeleteClick = (category: Category) => {
        setCategoryToDelete(category)
    }

    const handleDeleteConfirm = () => {
        if (categoryToDelete) {
            deleteMutation.mutate(categoryToDelete.id)
            setCategoryToDelete(null)
        }
    }

    const handleDeleteCancel = () => {
        setCategoryToDelete(null)
    }

    return (
        <div>
            <div className="mb-6 flex justify-between items-center">
                <h1 className="text-2xl font-semibold text-gray-900">Categories</h1>
                <Button onClick={handleAddCategory}>
                    Add Category
                </Button>
            </div>

            <div className="bg-white shadow rounded-lg divide-y divide-gray-200">
                {isLoading ? (
                    <div className="p-6 text-center">Loading...</div>
                ) : isError ? (
                    <div className="p-6 text-center text-red-500">
                        {error instanceof Error ? error.message : 'Error loading categories'}
                    </div>
                ) : !categories?.length ? (
                    <div className="p-6 text-center text-gray-500">No categories found</div>
                ) : (
                    categories.map((category) => (
                        <div key={category.id} className="p-6 flex justify-between items-center">
                            <div>
                                <h3 className="text-lg font-medium text-gray-900">{category.name}</h3>
                                {category.description && (
                                    <p className="mt-1 text-sm text-gray-500">{category.description}</p>
                                )}
                            </div>
                            <div className="flex gap-2">
                                <Button
                                    variant="outline"
                                    onClick={() => alert(`Edit category ${category.id}`)}
                                >
                                    Edit
                                </Button>
                                <Button
                                    variant="outline"
                                    onClick={() => handleDeleteClick(category)}
                                    className="text-red-600 hover:text-red-700"
                                    disabled={deleteMutation.isPending}
                                >
                                    {deleteMutation.isPending && category.id === categoryToDelete?.id
                                        ? 'Deleting...'
                                        : 'Delete'
                                    }
                                </Button>
                            </div>
                        </div>
                    ))
                )}
            </div>

            <AlertDialog open={!!categoryToDelete} onOpenChange={() => setCategoryToDelete(null)}>
                <AlertDialogContent>
                    <AlertDialogHeader>
                        <AlertDialogTitle>Are you sure?</AlertDialogTitle>
                        <AlertDialogDescription>
                            This will permanently delete the category &quot;{categoryToDelete?.name}&quot;
                            {categoryToDelete?.materials.length > 0 && (
                                <>
                                    <br /><br />
                                    <strong className="text-red-600">
                                        Warning: This category contains {categoryToDelete.materials.length} materials.
                                        Deleting it may affect existing candle recipes.
                                    </strong>
                                </>
                            )}
                        </AlertDialogDescription>
                    </AlertDialogHeader>
                    <AlertDialogFooter>
                        <AlertDialogCancel onClick={handleDeleteCancel}>Cancel</AlertDialogCancel>
                        <AlertDialogAction
                            onClick={handleDeleteConfirm}
                            className="bg-red-600 hover:bg-red-700 focus:ring-red-600"
                        >
                            Delete
                        </AlertDialogAction>
                    </AlertDialogFooter>
                </AlertDialogContent>
            </AlertDialog>
        </div>
    )
}
